@mixin col($name) {
	&[data-column="#{$name}"] {
		@content; } }

.order {
	font-family: $opensans;
	&__steps {
		width: 100%;
		border-bottom: 1px solid $inputBorder;
		padding-bottom: 15px;
		@include sm {
			display: none; }
		&_visible {
			@include sm {
				display: block; } } }
	&__steps-inner {
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		color: $gray-blue;
		font-weight: 600;
		font-size: 16px; }
	&__step {
		position: relative;
		padding-left: 70px;
		@include md {
			padding-left: 60px; }
		@include sm {
			font-size: 12px;
			padding-left: 40px; }
		&:first-child {
			padding-left: 0;
			&::before {
				display: none; } }
		&::before {
			position: absolute;
			top: 65%;
			left: 35px;
			content: url(../img/arrow.svg);
			width: 10px;
			transform: translate(-50%, -50%);
			@include sm {
				width: 8px;
				left: 21px; } } }
	&__step.is-active {
		color: $dark; }
	&__footer {
		font-family: $montserrat;
		padding: 40px 0;
		text-align: center;
		@include sm {
			padding-top: 20px; }
		p {
			text-align: center;
			font-size: 21px;
			padding-bottom: 20px;
			color: $gray;
			@include sm {
				font-size: 16px; } } }
	&__price {
		color: $dark; }
	&__btn {
		padding: 10px 10px 11px;
		max-width: 380px;
		width: 100%; } }

.counter {
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 125px;
	border-radius: 5px;
	border: 1px solid $inputBorder;
	padding: 7px 5px;
	font-size: 16px;
	&__input {
		padding-top: 2px;
		margin-left: 27px;
		max-width: 30px; }
	&__minus,
	&__plus {
		padding: 0 5px;
		color: $gray;
		font-size: 20px;
		line-height: 20px;
		background: none;
		border: none;
		margin-bottom: 3px;
		.icon-plus {
			width: 10px;
			height: 10px; } }
	&__minus {
		.icon-minus {
			width: 10px;
			height: 10px; } } }


.table {
	font-size: 0;
	margin-top: 45px;
	@include xmd {
		margin-top: 20px; }
	@include sm {
		margin-top: 0; }
	&__thead {
		@include sm {
			display: none; } }
	&__tr {
		border-bottom: 1px solid #dcdcdc;
		position: relative;
		padding-left: 100px;
		padding-right: 35px;
		@include xmd {
			padding-right: 25px; }
		@include sm {
			padding-right: 0;
			padding-left: 60px;
			padding-top: 3px;
			padding-bottom: 8px; } }
	&__td,
	&__th {
		display: inline-block;
		vertical-align: middle;
		padding-right: 10px;
		@include sm {
			padding-right: 0; }
		@include col("photo") {
			width: 100px;
			margin-left: -100px;
			@include sm {
				margin-left: -60px;
				width: 60px; } }
		@include col("name") {
			width: 38%;
			@include xmd {
				width: 41.6%; }
			@include sm {
				width: 100%; } }
		@include col("price") {
			width: 19.5%;
			@include xmd {
				width: 17%; }
			@include sm {
				display: none; } }
		@include col("qua") {
			width: 20%;
			@include xmd {
				width: 25%; }
			@include sm {
				width: 55%; } }
		@include col("value") {
			width: 22.5%;
			@include xmd {
				width: 16.4%; }
			@include sm {
				width: 45%; } }
		@include col("category") {
			width: 39%;
			@include sm {
				width: 100%; } } }
	&__th {
		padding-top: 8px;
		padding-bottom: 8px;
		@include f(11px, null, 19px);
		letter-spacing: 0.150em; }
	&__td {
		padding-top: 10px;
		padding-bottom: 10px;
		@include sm {
			padding-top: 7px;
			padding-bottom: 7px; } }
	&__img {
		display: block;
		width: 80px;
		height: 80px;
		@include vertical;
		@include sm {
			width: 50px;
			height: 50px; }
		&-in {
			max-width: 100%;
			max-height: 100%;
			display: inline-block;
			vertical-align: middle; } }
	&__name-label,
	&__name-value,
	&__price,
	&__value {
		@include f(14px, null, 26px);
		letter-spacing: .01em;
		@include sm {
			font-size: 13px; } }
	&__name-label {
		display: block;
		color: $dark;
		transition: color .3s;
		@include hover {
			color: $blue; } }
	&__name {
		@include sm {
			padding-right: 32px; } }
	&__name-value {
		color: rgba($dark, .5); }
	&__link {
		text-decoration: none;
		color: $dark;
		transition: color .3s;
		@include hover {
			color: $blue; } }
	&__value {
		@include sm {
			text-align: right; } }
	&__icon {
		fill: currentColor; }
	&__btn {
		position: absolute;
		right: 0;
		top: 50%;
		font-size: 15px;
		color: #4a5d70;
		background: none;
		padding: 10px;
		transform: translateY(-50%);
		@include xmd {
			padding-right: 0; }
		@include sm {
			font-size: 12px;
			transform: translate(0);
			top: 8px;
			padding-right: 10px; } } }



