.v-item {
	position: relative;
	padding: 15px;
	text-align: center;
	transition: box-shadow 0.3s;
	@include hover {
		box-shadow: 0 0 18px rgba(0, 0, 1, 0.16);
		.v-item {
			&__btns {
				opacity: 1;
				visibility: visible; } } }
	&__logo {
		position: absolute;
		top: 33px;
		right: 30px;
		transform: translateY(-50%);
		@include sm {
			top: 10px;
			right: 50%;
			transform: translate(50%, 0); } }
	&__figure {
		display: block;
		height: 197px;
		@include vertical; }
	&__img {
		display: inline-block;
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%; }
	&__btns {
		margin: 10px -6px 0;
		opacity: 0;
		visibility: hidden;
		@include clr;
		transition: opacity 0.3s, visibility 0.3s;
		@include xs {
			margin: 10px 0 0; }
		@include touch {
			opacity: 1;
			visibility: visible; } }

	&__control {
		float: left;
		padding: 0 6px;
		width: 50%;
		@include xs {
			width: 100%;
			padding: 0 0 10px; } }
	&__title {
		margin-bottom: 5px;
		max-height: 2em*1.5;
		color: $dark;
		@include f(14px, 400, 1.5);
		transition: color 0.3s;
		@include hover {
			color: $blue; } }
	&__price {
		@include f(14px, 700, 30px); } }
