.heading {
	padding: 20px 0;
	border-bottom: 1px solid #dcdcdc;
	@include xmd {
		padding: 15px 0; }
	@include xs {
		border: none; }
	&.border-bottom-zero {
		border-bottom: none; }
	&__title {
		margin-top: 13px;
		@include xmd {
			margin-top: 7px; }
		@include sm {
			margin-top: 0;
			text-align: center; } } }
