.catalog {
	&.catalog_installers {
		padding-top: 60px;
		.catalog__title {
			@include sm {
				text-align: center; } }
		.catalog__items {
			padding-top: 30px;
			padding-bottom: 45px; }
		.catalog__title {
			color: #202020; }
		.catalog__item {
			width: 25%;
			@include xmd {
				width: 50%; }
			@include xs {
				width: 100%; } } }
	&__inner {
		display: flex;
		@include md {
			flex-direction: column; } }
	&__sidebar {
		padding: 30px 20px 30px 0;
		border-right: 1px solid #dcdcdc;
		width: 280px;
		@include md {
			width: auto;
			padding: 0;
			margin: -1px -15px 0;
			display: none; }
		&.is-active {
			display: block; } }
	&__content {
		padding: 25px 0 45px 20px;
		flex-grow: 1;
		@include md {
			padding: 0 0 54px 0; }
		@include sm {
			padding: 0; }
		&.is-hidden {
			display: none; } }
	&__top {
		@include clr;
		&-left {
			float: left; }
		&-right {
			float: right;
			@include sm {
				float: none;
				width: 100%;
				text-align: center; } }
		.pagination__pages {
			@include xmd {
				padding: 0 15px; } } }
	&__bottom {
		text-align: center; }
	&__items {
		clear: both;
		padding-top: 40px;
		padding-bottom: 40px;
		margin: -10px;
		font-size: 0;
		@include sm {
			padding-bottom: 10px; }
		&.catalog__items_installers {
			padding-top: 20px;
			padding-bottom: 20px;
			.catalog__item {
				padding: 20px 10px; } } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 10px;
		width: 33.33%;
		@include xmd {
			width: 50%;
			padding-top: 20px;
			padding-bottom: 20px; }
		@include sm {
			padding-top: 0;
			padding-bottom:  15px; }
		@include xs {
			width: 100%; } } }
.checkbox_updated {
	padding-left: 30px; }

